import React, { useState, useEffect } from 'react'
import styles from './Country.module.css'
import CountryList from './countryApp/CountryList'
import Input from './countryApp/Input'


const Country = () => {

    const [inputValue, setInputValue] = useState('')
    const [data, setData] = useState([])
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(false)

    const inputChange = (name) => {
        setInputValue(name)
    }
    const isError = () => {
        setError(true)
    }

    const deleteCard = (name) => {
        setData(data.filter((country) => country.name !== name))

    }

    useEffect(() => {
        if (inputValue) {
            setLoading(true)

            fetch(`https://restcountries.com/v3.1/name/${inputValue}`)
                .then(res => res.json())
                .then((result) => {
                    console.log(result);
                    if (result[0]) {
                        const dataResult = result[0]
                        setData((prev) => {
                            return [...prev, {
                                name: dataResult.name.common,
                                flag: dataResult.flags.png,
                                population: dataResult.population,
                                region: dataResult.region,
                                capital: dataResult.capital,
                                id: Math.random(),

                            }]
                        })
                    } else {
                        isError()
                    }
                }).catch(() => {
                    isError()
                }).finally(() => {
                    setLoading(false)

                }
                )
        }
    }, [inputValue])

    return (
        <div className={styles.container}>
            <Input inputValue={inputChange} isError={error} input={inputValue} setError={() => setError(false)} />

            {loading ? <div className={styles.loading}></div> : <CountryList arrData={data} deleteCard={deleteCard} />}


        </div>
    )
}

export default Country