import styles from './Input.module.css'
import React, { useState } from 'react'

const Input = (props) => {
    const [input, setInput] = useState('')

    const submitForm = (event) => {
        event.preventDefault()

        props.inputValue(input);

        setInput('')
    }

    const changeInput = (event) => {
        setInput(event.target.value)
        props.setError()
    }



    return (
        <form className={styles.form} onSubmit={submitForm}>
            <div className={styles.animate}>
                <label className={styles.label} htmlFor="input" >
                    Country
                    <input className={styles.input} id="input" type="text" onChange={changeInput} value={input} />
                </label>
                {props.isError && <span className={styles.span}>{`${props.input} - is not defined`}</span>}
                <button className={styles.button} type='submit'>Add card</button>
            </div>
        </form>
    )
}

export default Input