import CountryCard from "./CountryCard"
import styles from './CountryList.module.css'
const CountryList = ({ arrData, deleteCard, inputValue }) => {

    return <ul className={styles.list}>
        {arrData.map(country => <CountryCard key={country.id} arrData={country} deleteCard={deleteCard} T={inputValue} />)}
    </ul >
}

export default CountryList